body {
    background-color: #111111;
    color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center; /* ← Centra verticalmente logo y botón */
    padding: 16px 32px;
}

.logo-wrapper {
    display: flex;
    align-items: center;
    gap: 10px;
}

.lovify-logo {
    width: 100px;
    height: 100px;
    object-fit: contain;
}

.brand {
    font-size: 60px;
    font-weight: bold;
}

.login-button {
    background: #ffffff;
    color: black;
    font-weight: bold;
    font-size: 14px;
    border: none;
    border-radius: 999px;
    padding: 14px 32px;
    cursor: pointer;
    transition: background 0.3s;
}